<template>
    <div>
        <h1>{{ message }}</h1>
        <form>
            <label for="">
                <input type="text" ref="name" v-model="state.name" placeholder="SessionName" required autofocus
                    class="form-control" />
            </label><br>
            <label for="">
                <input type="number" class="form-control" ref="laps" v-model="state.laps" placeholder="laps" />
            </label><br>
            <label for="">
                <input type="number" class="form-control" ref="rushTime" v-model="state.rushTime"
                    placeholder="rushTime" />
            </label><br />
            <label for="">
                <input type="number" class="form-control" ref="restTime" v-model="state.restTime"
                    placeholder="restTime" />
            </label><br />
            <SelectGoalList @setInput="goalid" /><br />
            <div class="box"></div>
            <SessionUploadPic />
            <label for="">
                <el-button type="primary" @click="registerHandler">Register</el-button>
            </label>
        </form>
    </div>
</template>

<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router';
import SessionUploadPic from './SessionUploadPic.vue'
import SelectGoalList from './SelectGoalList.vue';

export default {
    name: 'TheJoin',
    components: { SessionUploadPic, SelectGoalList },
    setup() {
        const router = useRouter();
        const state = reactive({
            goalid: '',
            uid: '',
            sessid: '',
            name: '',
            laps: '',
            rushTime: '',
            restTime: '',
            fileDTOList: new Array(),
            token: sessionStorage.getItem("TOKEN"),
            email: sessionStorage.getItem("email")
        })
        const goalid = (data) => {
            state.goalid = data;
        }
        const name = ref('')
        const laps = ref('')
        const rushTime = ref('')
        const restTime = ref('')

        const url = "http://localhost:9090/api/getUidWithEmail"
        const headers = {
            "Content-Type": "application/json",
            "Authorization": state.token,
            "token": state.token,
        }
        const body = { email: state.email }
        axios.post(url, body, { headers }).then(function (res) {
            if (res != null) {
                state.uid = res.data;
            } else {
                alert('too bad. check something')
            }
        })

        const registerHandler = async () => {
            let str = "";
            const liArr = document.querySelectorAll(".uploadResult ul li")

            for (let i = 0; i < liArr.length; i++) {
                const target = liArr[i];
                str += `

            <input type="hidden" name="fileDTOList[${i}].sessid"
            value="${target.dataset.sessid}">
            <input type="hidden" name="fileDTOList[${i}].uid"
            value="${target.dataset.uid}">
            <input type="hidden" name="fileDTOList[${i}].imgName"
            value="${target.dataset.name}">
            <input type="hidden" name="fileDTOList[${i}].path"
            value="${target.dataset.path}">
            <input type="hidden" name="fileDTOList[${i}].uuid"
            value="${target.dataset.uuid}">
          `
                const obj = {
                    sessid: state.sessid,
                    uid: state.uid,
                    imgName: target.dataset.name,
                    path: target.dataset.path,
                    uuid: target.dataset.uuid
                }
                state.fileDTOList.push(obj)
            }
            document.querySelector(".box").innerHTML = str

            const url = 'http://localhost:9090/api/sessionRegister'
            const headers = {
                "Content-Type": "application/json",
                Authorization: state.token,
                "token": state.token
            }
            const body = {
                name: state.name, laps: state.laps, rushTime: state.rushTime, restTime: state.restTime,
                fileDTOList: state.fileDTOList, goalid: state.goalid, uid: state.uid, sessid: state.sessid,
            }
            const response = await axios.post(url, body, { headers })
            if (response.status === 200) {
                alert('wow successfully uploaded üòó');
            } else {
                alert('failed to upload')
            }
        }

        return { state, name, laps, rushTime, restTime, goalid, registerHandler }
    },
    data() {
        return { message: 'Session Maker' }
    },
};
</script>

<style scoped>
label {
    display: inline-block;
    width: 500px;
    text-align: right;
    color: red;
    margin: 10px 0;
}

/* @import url(../assets/commonstyle.css); */
</style>